<template>
  <div id="profile">
    <img :src="p.photoURL" />
    <h1 :class="'status ' + p.status">{{ p.displayName }}</h1>
    <!--<br>global {{p.aboutMe}} <br>local {{aboutMe}}<hr>-->
    <div
      :title="p.uid == u.uid ? 'Uprav text' : ''"
      :class="p.uid == u.uid ? 'clickable' : ''"
      v-if="
        Object.keys(this.p).length == 1 ||
          (this.p.aboutMe && !editing) ||
          p.uid != u.uid
      "
      @click="edit"
      v-html="format_text(p.aboutMe)"
    ></div>
    <div v-else>
      <textarea
        ref="textarea"
        rows="10"
        v-model="aboutMe"
        @blur="
          $emit('aboutMe', aboutMe ? aboutMe : null);
          editing = false;
        "
      >
      </textarea>
    </div>
  </div>
</template>

<script>
export default {
  props: ["p", "u", "format_text" /*,"focus"*/],
  data: function() {
    return {
      editing: false,
      //aboutMe:this.p.aboutMe neni reaktivni nejspis, ze chci jen jednu slozku objektu a ne cely
      aboutMe: null
    };
  },
  //created(){this.aboutMe=this.p.aboutMe},
  methods: {
    edit() {
      this.editing = true;
      this.aboutMe = this.p.aboutMe; //muselo se pridat, protoze se nedalo dosahnout reaktivnosti
      this.focus();
    },
    //focus zatim nic nedela
    //ESC zavira
    focus() {
      //vklada se kvuli this, ktery je jiny pri volani z app.vue a u @click this je null, props zatim nevyresen
      this.$nextTick(() => {
        //autosize(this.$refs[ref]);
        //this.$root.window.alert(45545)
        this.$w.console.log("focus");
        this.$w.console.log(this.$refs.textarea);
        if (!this.$refs.textarea) return;
        this.$refs.textarea.focus();
      });
    }
  }
};
</script>
<style>
#profile {
  max-width: 800px;
  margin: 0 auto;
  margin-bottom: 12px;
  text-align: center;
}
#profile img {
  width: 150px;
}
.clickable {
  cursor: pointer;
}
.online:before {
  color: green;
}
.offline:before {
  color: gray;
}
.status:not(:empty) {
  content: "🔴";
  content: "\25CF";
  margin-right: 3px;
}
</style>
